import React from "react";
import { cn } from "../../lib/utils";

const Switch = React.forwardRef(({ className, checked, ...props }, ref) => {
  return (
    <div
      className={cn(
        "peer inline-flex h-6 w-11 shrink-0 cursor-pointer items-center rounded-full border-2 border-transparent transition-colors",
        "focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-[hsl(var(--primary))]", // Updated focus style
        "disabled:cursor-not-allowed disabled:opacity-50",
        checked ? "bg-primary" : "bg-input",
        className
      )}
    >
      <span
        className={cn(
          "pointer-events-none block h-5 w-5 rounded-full bg-background shadow-sm ring-0 transition-transform", // Changed shadow-lg to shadow-sm
          checked ? "translate-x-5" : "translate-x-0"
        )}
      />
      <input
        type="checkbox"
        ref={ref}
        checked={checked}
        className="sr-only"
        {...props}
      />
    </div>
  );
});

Switch.displayName = "Switch";

export { Switch };
